import React, { Component } from 'react'
import { Row, Col } from 'antd'

export default class Category extends Component {
  state ={
    hover: false,
  }

  handleEnter () {
    this.setState((state) => {
      return {
        hover: true,
      }
    })
    this.props.handleHover()
  }

  handleLeave () {
    this.setState((state) => {
      return {
        hover: false,
      }
    })
  }

  render () {
    let jumbo = null
    if (this.state.hover) {
    // if (true) {
      jumbo = (
        <div
          style={{
            position: 'fixed',
            top: `${this.props.subheight}px`,
            left: 0,
            width: `${this.props.subwidth}px`,
            height: '200px',
            background: 'rgba(37, 38, 37, 1.0)',
          }}
        >
          <CategoryContent></CategoryContent>
        </div>
      )
    }
    let fw = 500
    if (this.state.hover) fw = 500
    return (
      <div
        onMouseEnter={ this.handleEnter.bind(this) }
        onMouseLeave={ this.handleLeave.bind(this) }
        style={{ height: '60px' }}
      >
        <a href="/">
          <span
            style={{
              position: 'absolute',
              lineHeight: '22px',
              marginTop: '10px',
              fontSize: '16px',
              color: '#fff',
              fontWeight: fw,
            }}
          >
            <div dangerouslySetInnerHTML={{ __html: '&nbsp&nbsp&nbsp&nbsp&nbsp' }}></div>
          </span>
        </a>
      </div>
    )
  }
}

class Section extends Component {
  render () {
    return (
      <div>
        <h2 style={{ color: 'white' }}>
          { this.props.title }
        </h2>
        <p>
          { this.props.content }
        </p>
      </div>
    )
  }
}

