import React,{Component} from 'react';
import { Row, Col} from 'antd';
import MyIcon from '../../my_icon'
const intro1='F58CGAFB26 JVQMC2F';
const str1 = intro1.slice(0,150)+'...';
const intro=(<span>&nbsp;&nbsp;&nbsp;&nbsp;{intro1}</span>)
const str=(<span>&nbsp;&nbsp;&nbsp;&nbsp;{str1}</span>)
const img1 =
  "https://pic.36krcnd.com/201804/21023539/7g8t9syx7cwv6lrm.png!1200";


export default class Intro extends Component{
  state = { closeIntro: true,show:str }
  
  showModal = () => {
    this.setState({
      closeIntro: !this.state.closeIntro,
      show:this.state.closeIntro?intro:str
    });
  }
  
  render(){
    return(
    <div>
       <Row >
          <Col>
            <div style={{borderTop:'1px solid rgba(216,216,216,1)'}}></div>
            <div style={{margin:'2% 0'}}>
              <Row>
                <Col span={4}>
                  <img src={img1}  style={{width:'100%' }}/>
                </Col>
                <Col span={1} />
                <Col span={18}>
                  <Row>
                    <Col span={24}>
                      <div>
                        {this.state.show}
                      </div>
                    </Col>
                  </Row>
                  
                </Col>
                <Col span={1} />
              </Row>
              <Row>
              
                <Col span={8} />
                <Col span={8} >
                <br/>
                  <div style={{color:'#FF1367',margin:'0 20%'}} onClick={this.showModal} >{this.state.closeIntro?
                  (<span><MyIcon type='icon-spread'/></span>):(<span><MyIcon type='icon-shrink'/></span>)}</div>
                </Col>
                <Col span={8} />
              </Row>
          </div></Col>
        </Row>
    </div>
    )
  }
}
